let url = process.env.REACT_APP_LOCAL_URL
if (!url) url = "https://thereweather.space/api"

export default function PostRead() {
    const history = useHistory()
    const { readPostId, userInfo, postInfo } = useSelector(
        (state) => state.itemReducer
    )
    // console.log("  ",userInfo) // 
    // console.log(" : ",readPostId) //
    // console.log(postInfo) // 
    const postIds = Number(readPostId)
    // console.log(postIds)

    // postData state 
    const [postData, setPostData] = useState({
        id: null,
        post_title: "",
        user_id: "",
        createdAt: "",
        updatedAt: "",
        xLocation: "",
        yLocation: "",
        post_photo: "",
        weather: "",
        wind: "",
        temp: "",
        top_id: "",
        bottom_id: "",
        post_content: "",
        address: "",
    })
    const [noIdWarning, setNoIdWarning] = useState("")

    //  
    const formatDate = (dateString) => {
        //  : 2021. 11. 5. 22:02
        const dateObject = new Date(dateString)
        let dateOnly = dateObject.toLocaleDateString()
        let hourAndMin = dateObject.toLocaleTimeString("en-US", {
            hour12: false,
        })
        hourAndMin = hourAndMin.slice(0, -3)

        return `${dateOnly} ${hourAndMin}`
    }

    let currentPostId = 0   // post id 
    //  
    useEffect(() => {
        function getOnePost(postId) {
            axios
                .get(`${url}/readpost`, {
                    headers: { "Content-Type": "application/json" },
                    params: { id: postId },
                    withCredentials: true,
                })
                .then((res) => {
                    // console.log("  : ",res.data)
                    return setPostData((prev) => res.data)
                })
                .catch((err) => console.log(err))
        }

        let id
        if (history.location.state) {
            id = history.location.state.postId
        } else {
            id = readPostId
        }

        if (!id) {
            //console.log("**postread: id **")
            setNoIdWarning((prev) => " .")
        } else {
            getOnePost(id)
            currentPostId = id
        }

        console.log("  id : ", id)
        console.log("currentPostId : ", currentPostId);
    }, [])

    //  
    const [bookmarked, setBookmarked] = useState(false)

    // const [isOpen, setIsOpen] = useState(false);
    //  
    const [edit, setEdit] = useState(false)
    //  
    const [removePost, setRemovePost] = useState(false)

    //  
    const editPost = () => {
        //console.log("")
        setEdit(true)
    }

    //  
    const deletePost = (e) => {
        //console.log("")
        setRemovePost(true)
    }

    //  yes
    const editModalYes = () => {
        axios({
            url: url + "/editpost",
            method: "put",
            data: {
                user_id: userInfo.user_id,
                post_id: postIds,
            },
            withCredentials: true,
        }).then((res) => {
            alert(res.data)
            if (res.data === "  .") {
                history.push("/mypage")
            } else {
                history.push("/editpost")
            }
        })
        setEdit(false)
    }

    //  yes
    const removeModalYes = () => {
        // console.log('')
        const token = JSON.parse(localStorage.getItem("ATOKEN"))
        //console.log(token)
        axios({
            url: url + "/deletepost",
            method: "delete",
            headers: {
                "Content-Type": "application/json",
                Authorization: `token ${token}`,
            },
            data: { post_id: postIds },
            withCredentials: true,
        }).then((res) => {
            //console.log(res.data)
            alert(res.data)
            // alert(" ")
            history.push("/mypage")
        })
        setRemovePost(false)
    }

    const modalNoButtonHandler = () => {
        setRemovePost(false)
        setEdit(false)
    }

    const modalCloseButtonHandler = () => {
        setRemovePost(false)
        setEdit(false)
    }

    const bookmarkHandler = (e) => {
        //console.log("  -    ")
        axios({
            url: url + "/bookmark",
            method: "post",
            data: { user_id: userInfo.id, post_id: postIds },
            // data: { post_id: postId },
            headers: { "Content-Type": "application/json" },
            withCredentials: true,
        }).then((res) => {
            //console.log(res.data)
            setBookmarked((prev) => !prev)
            // history.push("/bookmark")
            // window.location.replace("/readpost")
        })
        // console.log(e.currentTarget);
    }

    //   
    useEffect(() => {
        getCommentList()
    }, [])

    // ,  
    const [commentMsg, setCommentMsg] = useState(null)
    const [commentList, setCommentList] = useState([])

    const commentBtnClick = () => {
        if(commentMsg === null) {
            alert(" .")
        }else{
            axios({
                url: url + "/sendcomment",
                method:  "post",
                data: {
                    post_id: postData.id,
                    comment_user_id: userInfo.user_id,
                    comment_content: commentMsg,
                },
                withCredentials: true,
            })
            .then(() => {
                getCommentList()
                window.location.replace("/readpost")
            })
        }
    }
    //  
    function getCommentList() {
        axios({
            url: url + "/commentlist",
            headers: { "Content-Type": "application/json" },
            method: "get",
            params: {
                post_id: currentPostId,
            },
            withCredentials:true,
        })
        .then((res) => {
            // console.log(" : ", res.data);
            setCommentList(res.data)
        })
    }
    const inputHandle = (e) => {
        setCommentMsg(e.target.value)
    }

    // 
    const commentDelete = (commentId) => {
        const token = JSON.parse(localStorage.getItem("ATOKEN"))
        axios({
            url: url + "/deletecomment",
            method: "delete",
            headers: { 
                "Content-Type": "application/json", Authorization: `token ${token}`, 
            },
            data: {
                comment_id: commentId, //  
            },
            withCredentials: true,
        })
        .then((res) => {
            window.location.replace("/readpost")
        })
    }

    useEffect(() => {
        axios({
            url: url + "/readbookmark",
            method: "post", 
            data: {
                user_id: userInfo.id, 
                post_id: postIds
            }, 
            headers: { "Content-Type": "application/json" },
            withCredentials: true
        })
        .then((res) => {
            //console.log(res.data)
            if(res.data !== ""){
              setBookmarked(!bookmarked)
            }
        })
    }, [])

    return (
        <Outer>
            {noIdWarning.length !== 0 ? (
                <h2 className="warning--nodata">{noIdWarning}</h2>
            ) : (
                ""
            )}
            <TopButton />
            <GoBackButton />
            <PostHeader className="postHeader">
                <Title className="title">
                    <span>{postData.post_title}</span>
                    <BookmarkIcon
                        bookmarkHandler={bookmarkHandler}
                        color={bookmarked ? "#3b5fd9" : "#aaa"}
                    />
                </Title>

                <Profile className="userProfile">
                    <div className="profileInfo">
                        <ProfileImg src={postData.user_Photo} />
                        <span className="nickName">{postData.nickName}</span>
                        <span className="date">
                            {formatDate(postData.updatedAt)}
                        </span>
                    </div>
                    <div>
                        <p className="location">{postData.address}</p>
                        {/* <p className="location">{postData.xLocation.slice(0, -8)}</p>
            <p className="location">{postData.yLocation.slice(0, -8)}</p> */}
                    </div>
                </Profile>
            </PostHeader>
            <PostImg src={postData.post_photo} alt="post picture" />

            <WeatherInfo>
                {!postData.weather ? (
                    ""
                ) : (
                    <Icon
                        src={`${process.env.PUBLIC_URL}img/icons-write/${postData.weather}.png`}
                        alt=""
                    />
                )}
                {!postData.wind ? (
                    ""
                ) : (
                    <Icon
                        src={`${process.env.PUBLIC_URL}img/icons-write/${postData.wind}.png`}
                        alt=""
                    />
                )}
                {!postData.temp ? (
                    ""
                ) : (
                    <Icon
                        src={`${process.env.PUBLIC_URL}img/icons-write/${postData.temp}.png`}
                        alt=""
                    />
                )}
            </WeatherInfo>

            {/*   ,   */}
            {
                //  3 
                (!postData.outer_id || postData.outer_id === "default") &&
                (!postData.top_id || postData.top_id === "default") &&
                (!postData.bottom_id ||
                    postData.top_id === "default") ? null : (
                    <>
                        <h2 className="todayCodi"> </h2>
                        <TodayCodi>
                            {!postData.outer_id ||
                            postData.outer_id === "default" ? (
                                <p className="warning">
                                      
                                </p>
                            ) : (
                                <Icon
                                    src={`${process.env.PUBLIC_URL}img/codi/${postData.outer_id}.png`}
                                    alt=""
                                />
                            )}
                            {!postData.top_id ||
                            postData.top_id === "default" ? (
                                <p className="warning">
                                      
                                </p>
                            ) : (
                                <Icon
                                    src={`${process.env.PUBLIC_URL}img/codi/${postData.top_id}.png`}
                                    alt=""
                                />
                            )}
                            {!postData.bottom_id ||
                            postData.top_id === "default" ? (
                                <p className="warning">
                                      
                                </p>
                            ) : (
                                <Icon
                                    src={`${process.env.PUBLIC_URL}img/codi/${postData.bottom_id}.png`}
                                    alt=""
                                />
                            )}
                        </TodayCodi>
                    </>
                )
            }

            <Post>
                <p>{postData.post_content}</p>
            </Post>

            <Buttons>
                <button
                    className="button button1"
                    value="delete"
                    onClick={deletePost}
                >
                    
                </button>
                {removePost === false ? null : (
                    <ModalConfirm
                        yesHandler={removeModalYes}
                        noHandler={modalNoButtonHandler}
                        closeHandler={modalCloseButtonHandler}
                    >
                        ?
                    </ModalConfirm>
                )}
                <button className="button button2" onClick={editPost}>
                    
                </button>
                {edit === false ? null : (
                    <ModalConfirm
                        yesHandler={editModalYes}
                        noHandler={modalNoButtonHandler}
                        closeHandler={modalCloseButtonHandler}
                    >
                        ?
                    </ModalConfirm>
                )}
            </Buttons>

            <CommentSection>
                {/*  */}
                <PostComment>
                    <input type="text" placeholder=" ." onChange={inputHandle}/>
                    <button onClick={commentBtnClick}></button>
                </PostComment>
                {/*  */}
                <CommentList>
                    {commentList.map((content) => (
                        <Comment
                            key={content.id}
                            content={content}
                            commentDelete={commentDelete}
                        />
                    ))}
                </CommentList>
            </CommentSection>

        </Outer>
    )
}
