let url = process.env.REACT_APP_LOCAL_URL

export default function FindAccount(){
    const dispatch = useDispatch()
    if (!url) {
        url = "https://thereweather.space/api"
    }

    const [inputFindInfo, setInputFindInfo] =  useState({
        findNickName: "",
        authEmail: "",
        authCode: "",
    })
    
    useEffect(() => {
        dispatch(changeMapPage(false))
    }, [])

    const ChangeHanlder = (key) => (e) => {
        setInputFindInfo({
            ...inputFindInfo,
            [key]: e.target.value,
        })
    }

    function sendEmail() {
      //console.log("  ")
      if(inputFindInfo.findNickName && !inputFindInfo.authCode){
        axios({
          url: url + "/users/auth",
          method: "post",
          headers: {
            "Content-Type": "application/json",
          },
          data: {
            temporary_id: inputFindInfo.findNickName,
            email: inputFindInfo.authEmail
          },
          withCredentials: true
        })
        .then((res) => {
            console.log(res.data.status)
            if(res.data.status === "success"){
                alert(" . 3 :)")
            }else{
                alert("  .")
            }   
        })
      }else{
        alert("  .")
      }
    }

    function verification() {
      //console.log(" ")
      axios({
        url: url + "/users/auth",
        method: "put",
        headers: {
          "Content-Type": "application/json", 
        },
        data: {
          temporary_id: inputFindInfo.findNickName,
          email: inputFindInfo.authEmail,
          code: inputFindInfo.authCode
        },
        withCredentials: true
      })
      .then((res) => {
        // console.log(res.data)
        if(res.data.status === "true"){ 
            alert("  .")
        }else{
            alert("  .  :)")
        }
      })
    }

    function findAccountId() {
      console.log("  ")
      //, ,      
      //     -> , ,  
       
    }
    
    
    //   
    const submitBtn = () => {

    }


    return (
        <Outer>
            <Form>
                <Div1>
                    <p id="title"> </p>
                    <p> </p>
                </Div1>

                <Div2>
                    <ul>
                        <li>
                            <p></p>
                            <input type="text" onChange={ChangeHanlder("findNickName")}></input>
                        </li>
                        <li>
                            <p></p>
                            <input type="text" onChange={ChangeHanlder("authEmail")}></input>
                            <button onClick={sendEmail}></button>
                        </li>
                        <li>
                            <p></p>
                            <input type="text" onChange={ChangeHanlder("authCode")}></input>
                            <button onClick={verification}></button>
                        </li>
                    </ul>
                </Div2>
                <Button onClick={findAccountId}> </Button>
            </Form>
        </Outer>
    )
}